svg {
    stroke-width: 1px;
}
.grid {
    stroke: #939ba1;
}
.label {
    fill: black;
    font-size: 14px;
}
.all {
    stroke: #6688bb;
    fill: #99bbdd;
}
.selected {
    stroke: #6688bb;
    fill: #6688bb;
    stroke-width: 2px;
}
.strokeAll {
    stroke: #99bbdd;
    fill: none;
}
.strokeSelected {
    stroke: #6688bb;
    fill: none;
    stroke-width: 2px;
}
.fillAll {
    stroke: #99bbdd;
    fill: #99bbdd;
}
.fillSelected {
    stroke: #6688bb;
    fill: #6688bb;
}
